<template>
  <div :class="'red position-relative ' +(brnot?'':' box')" :style="{height: height+'px', width: width}">
    <div :class="'green position-relative ' +(brnot?'':' box')" :style="{height: height+'px', width: filled+'%'}">
    </div>
  </div>
</template>

<script>
export default {
  mounted(){
    this.filled = this.fill
  },
  props: {
    height: String,
    width: String,
    fill: String,
    brnot: Boolean,
  },
  data(){
    return{
      filled: '100',
    }
  },
  watch:{
    fill(fill){
      this.filled = fill
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .red{
    background: #F75050;
  }
  .box{
    border-radius: 0 0 5px 5px;
  }
  .green{
    transition: width 1s cubic-bezier(0, 0, 1, 1) 100ms;
    background:rgb(68 142 66);
  }

</style>
